import React, { useEffect, useState } from 'react'
import { db } from '../../Firebase';
import firebase from 'firebase/app'
import { useAuth } from '../../Context/AuthContext';
import AboutPost from './AboutPost';

export default function AboutPostContainer(props) {

    const [info, setInfo] = useState('')
    const id = props.match.params.id;
    const [disabled, setDisabled] = useState(true)
    const [show, setShow] = useState(false);
    const [likes, setLikes] = useState(0)
    const [comments, setComments] = useState([])
    const [comment, setComment] = useState('')
    const {currentUser} = useAuth()

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    useEffect(() => {
        db.collection('posts').doc(id).get()
            .then(snapshot => setInfo(snapshot.data()))
    }, [])

    function handleDelete() {
        db.collection('posts').doc(id).delete()
    }

    function onLike() {
        setLikes(likes + 1)
    }

    function postComment(e) {
        e.preventDefault()

        db.collection('posts').doc(id).collection('comments').add({
            comment: comment,
            username: currentUser.email,
            timestamp: firebase.firestore.FieldValue.serverTimestamp(),
        })
        setComment('')
    }

    return (
        <AboutPost info={info} show={show} handleClose={handleClose} handleShow={handleShow} disabled={disabled} 
        handleDelete={handleDelete} onLike={onLike} comment={comment} likes={likes} postComment={postComment} setComment={setComment}/>
    )
}
